/* main font */
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
  border: 0;
  list-style: none;
  text-decoration: none;
}
:root{
  --primary-hue:210;
  --gray-hue:240;
  --color-primary:hsl(var(--primary-hue),100%,50%);
  --color-primary-variant:hsl(var(--primary-hue),100%,65%);
  --color-secondary:#ffb116;

--color-gray-100:hsl(var(--gray-hue),47%,94%);
--color-gray-200:hsl(var(--gray-hue),26%,75%);
--color-gray-300:hsl(var(--gray-hue),24%,64%);
--color-gray-400:hsl(var(--gray-hue),16%,41%);
--color-gray-500:hsl(var(--gray-hue),44%,25%);
--color-gray-600:hsl(var(--gray-hue),48%,15%);

--container-width-lg:80%;
--container-width-md:90%;

--transition:all 0.5s ease;

}
body{
  font-family: 'Montserrat', sans-serif;
color: var(--color-gray-200);
line-height: 1.7;
overflow-x: hidden;
background: var(--color-gray-600);
background-image: url('./images/bg_texture.png');
}

/* general styles */

.container{
  width: var(--container-width-lg);
  max-width: 1920px;
  margin-left: auto;
  margin-right: auto;

}
h1,h2,h3,h4,h5{
  line-height: 1.2;
  color: var(--color-gray-100);
}
h1{
  font-size: 3rem;
}
h2{
  font-size: 2rem;
}
a{
  color: var(--color-gray-100);
}
img{
  display: block;
  object-fit: cover;
  width: 100%;
} 
.btn{
  color: var(--color-gray-100);
  width: fit-content;
  margin: 0 auto;
  padding: 0.9rem 2rem;
  background: var(--color-primary);
  border-radius: 1.5rem;
  transition: var(--transition);
  cursor: pointer;
}
.btn:hover{
 background: var(--color-secondary); 
 color: var(--color-gray-600);
}
.btn.lg{
  padding: 1.2rem 3rem;
  border-radius: 2rem;
  font-size: 1.1rem;
}
.btn.sm{
  padding: 0.4rem 1.2rem;
  font-size: 0.9rem;
}
.btn.sm:hover{
  background: var(--color-gray-100);
}
section{
  margin-top: 10rem;
}
.section-head{
  display: flex;
  align-items: center;
  gap: 1rem;
}
.section-head span{
  background: var(--color-gray-500);
  padding: 0.8rem;
  border-radius: 1rem;
  color: var(--color-secondary);
  font-size: 1.5rem;
}


.card{
  background: var(--color-gray-500);
  border: 2px solid transparent;
  text-align: center;
  padding: 3rem 2rem;
  border-radius: 0 3rem 0 3rem;
  transition: var(--transition);
}
.card:hover{
  border-color:var(--color-gray-400) ;
  background: transparent;
  cursor: default;
}
.card span{
  height: 3rem;
  width: 3rem;
  font-size: 1.5rem;
  padding: 0.8rem;
  background: var(--color-primary);
  display: grid;
  place-items: center;
  margin-inline: auto;
  margin-bottom: 1.5rem;
  border-radius: 1rem;
  transition: var(--transition);
}
.card:hover span{
  background: var(--color-secondary);
  color: var(--color-gray-600);
}
.card small{
  display: block;
  margin-top: 1rem;
font-weight: 300;
  
}



/* media queries */

@media (max-width:1024px) {
    .container{
      width: var(--container-width-md);
    }
    h1{
      font-size: 2rem;
    }
    h2{
      font-size: 1.6rem;
    }
}

@media (max-width:600px) {
  .container{
    width: var(--container-width-md);
  }
  h1{
    font-size: 2.2rem;
    line-height: 1.3;
  }
  section{
    margin-top: 7rem;
  }

}