.contact-container{
  display: grid;
  place-items: center;
}
.contact-wrapper{
  width: fit-content;
  display: flex;
  gap: 2rem;
}
.contact-wrapper a {
  background: var(--color-primary);
  font-size: 2rem;
  border: 2px solid transparent;
  transition: var(--transition);
  padding: 2rem;
  border-radius: 1.5rem;
}
.contact-wrapper a:hover{
  background: transparent;
  border-color: var(--color-gray-100);
}


@media (max-width:600px) {
  .contact-wrapper{
    gap: 1.5rem;
  }
  .contact-wrapper a{
    padding: 1rem;
    font-size: 1.2rem;
    border-radius: 1rem;
  }
}