nav{
  width: 100vw;
  height: 5rem;
  background: var(--color-primary);
  display: grid;
  place-items: center;
  position: fixed;
  top:0;
  left: 0;
  z-index: 99;
}
/* only show when mobile and tablets */
.nav-toggle-btn{
  display: none;
}
.nav-container{
  height: 100%;
  display: flex;
  justify-content: space-between;
align-items: center;
position: relative;
}
.logo{
  width: 7rem;
  display: block;

}
.nav-links{
  display: flex;
  align-items: center;
  gap: 3.5rem;
}
.nav-links a{
  transition: var(--transition);
}
.nav-links a:hover{
  color: var(--color-secondary);
}
.nav-link.active{
  position: relative;
}

.nav-link.active::after{
  content: '';
  display: block;
  width: 1.2rem;
  height: 1.2rem;
  background: var(--color-primary);
  position: absolute;
left: calc(50% - 0.6rem);
transform: rotate(45deg);
margin-top: 0.9rem;
}

/* media queries for mobile and tablets */

@media (max-width:1024px) {
    .nav-toggle-btn{
      display: inline-block;
      background: transparent;
      cursor: pointer;
      font-size: 1.8rem;
      color: var(--color-gray-100);
    }
    .nav-link.active::after{
        display: none;
    }
    
    .nav-links{
      position: absolute;
      right: 0%;
      top: 100%;
      flex-direction: column;
      gap: 0;
      perspective: 700px;
    }
    .show-nav{
      display: flex;
    }
    .hide-nav{
      display: none;
    }
    /* .active-nav::after{
      display: none;
    } */
    .nav-links li{
      height: 4rem;
      width: 100%;
      box-shadow: -2rem 2rem 5rem rgba(0, 0, 0, 0.4);
      animation: navMenu 1S ease forwards ;
      transform-origin: top right;
      opacity: 0;
      transform: rotate(-90deg) rotateY(90deg);
    }
    .nav-links li a {
      background-color: var(--color-primary);
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      padding: 1rem 5rem 1rem 3rem;
    
    }
    .nav-links li:nth-child(2)
    {
      animation-delay: 200ms;
      transform-origin: top left;
      transform: rotate(90deg) rotateY(-90deg);

    }
    .nav-links li:nth-child(3)
    {
      animation-delay: 300ms;
    }
    .nav-links li:nth-child(4)
    {
      animation-delay: 400ms;
      transform-origin: top left;
      transform: rotate(90deg) rotateY(-90deg);
    }
    .nav-links li:nth-child(5)
    {
      animation-delay: 500ms;
    }
    .nav-links li:nth-child(6)
    {
      animation-delay: 600ms;
      transform-origin: top left;
      transform: rotate(90deg) rotateY(-90deg);
    }
   
   @keyframes navMenu {

  100%{
    opacity: 1;
    transform: rotate(0) rotateY(0);

  }
}
}
