.programs{
  margin-top: 4rem;
}
.programs-wrapper{
  display: grid;
  grid-template-columns: repeat(4,1fr);
  gap: 3rem;
  margin-top: 4rem;
}
.programs-wrapper a {
  margin-top: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.prorams-program:hover  a {
background: var(--color-secondary);
color: var(--color-gray-600);
}